.contactsBlock{
    background-color: #bce7a1;
    padding: 20px 0;
}

.contacts{
    flex-direction: column;
    justify-content: space-between;
    gap: 40px;
}

.title{
    background-color: #e18282;
}
.form{
    border: 1px solid red;
    gap: 10px;
    display: flex;
    flex-direction: column;
    width: 280px;
}
.textareaDescription{
    min-height: 100px;
}
.buttonSend{
    background-color: #6476ed;
    color: white;
    width: 200px;
    height: 40px;
}