.project{
    display: flex;
    flex-direction: column;
    width: 360px;
}

.imageBackground{
    background-color: grey;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 240px;
    background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRMoMxZPMc0ol3ZVU2tviOotzJOaNogyXK6NQ&usqp=CAU");
    background-size: cover;
    background-position: center;
}
.linkToProject{
    background-color: #6476ed;
    color: white;
    padding: 10px 40px;
    text-decoration: none;
    text-align: center;
}

.projectDescription{
    padding: 10px;
    border: 3px solid #5cc926;
}
.title {
    background-color: grey;
}
.description{
    background-color: grey;
}